<script>
    import { onMount } from 'svelte';
    import { createEventDispatcher } from 'svelte';
    import Card from '@smui/card';
    import Button, { Label } from '@smui/button';
    import Fab, { Icon } from '@smui/fab';
    import Key from './Key.svelte';

    import Textfield from '@smui/textfield'

    const dispatch = createEventDispatcher();



    function type(number) {
        dispatch("key", { 'number': number });
    }

    function onKeyClicked(data) {
        dispatch("key", data.detail);
    }

    function clear() {
        dispatch("clear",{});
    }

</script>

<style>
    .button {
        margin: 10px 10px 10px 10px;
    }
</style>

<div style="margin: 0 auto; width: 100%;  flex-wrap:wrap; padding: 10px;display:flex;flex-direction: column;">

    <div style="margin: 0 auto; width: 100%;  flex-wrap:wrap; padding: 10px;display:flex;flex-direction: row;">
        <Key number="7" on:click={onKeyClicked}></Key>
        <Key number="8" on:click={onKeyClicked}></Key>
        <Key number="9" on:click={onKeyClicked}></Key>
    </div>

    <div style="margin: 0 auto; width: 100%;  flex-wrap:wrap; padding: 10px;display:flex;flex-direction: row;">
        <Key number="4" on:click={onKeyClicked}></Key>
        <Key number="5" on:click={onKeyClicked}></Key>
        <Key number="6" on:click={onKeyClicked}></Key>
    </div>

    <div style="margin: 0 auto; width: 100%;  flex-wrap:wrap; padding: 10px;display:flex;flex-direction: row;">
        <Key number="1" on:click={onKeyClicked}></Key>
        <Key number="2" on:click={onKeyClicked}></Key>
        <Key number="3" on:click={onKeyClicked}></Key>

    </div>
    <div style="margin: 0 auto; width: 100%;  flex-wrap:wrap; padding: 10px;display:flex;flex-direction: row;">
        <div class="button">
            <Button variant="raised" ripple="true" color="secondary" class="row">
            </Button>
        </div>

        <Key number="0" on:click={onKeyClicked}></Key>
        <div class="button">
            <Button variant="raised" ripple="true" color="secondary" class="row" on:click={clear}>
                <Label>C</Label>
            </Button>
        </div>
    </div>
</div>