<script>
    import Switch from '@smui/switch';
    import FormField from '@smui/form-field';
    import Card from '@smui/card';
    import { switched } from './store.js';

    switched.useLocalStorage();

    let selected = false;

    const unsubscribe = switched.subscribe(value => {
		  selected = value;
    });
    
    let toggle = function() {
      switched.update(n => !n);
    }
  </script>
  
  <div class="container">
    <Card >
  <FormField>
    <Switch checked={selected} on:click={toggle} />
    <span slot="label">{selected ? 'ON' : 'OFF'}</span>
  </FormField>
  </Card>
  </div>
