<script>

    import Card from '@smui/card';
    import Button, {Label, Icon} from '@smui/button';
    
    import Textfield from '@smui/textfield'
    import { onMount } from 'svelte';

let toBeGuessed = 0;
let guesses = 0;
let isFound = false;
let isLower = false;
let isUpper = false;
let currentGuess = "votre essai";
let trying = false;


onMount(async () => {
            reset();
        });

function reset() {
    toBeGuessed = Math.floor(Math.random() * Math.floor(100));
    guesses = 0;
}

function guess() {
    guesses++;    
    console.log(`${currentGuess} ?? ${toBeGuessed}`);
    isFound = toBeGuessed == currentGuess;
    isLower = toBeGuessed < currentGuess;
    isUpper = toBeGuessed > currentGuess;
}

</script>

<div class="container">
    <Card>
        <!-- ici bouton reset -->
        <Button on:click={reset}>
            <Icon class="material-icons">autorenew</Icon>
            <Label>Nouveau</Label>
        </Button>        
        <!-- ici nombre d'essais -->
        <span>{guesses} essai{guesses > 0 ? 's':''}</span>
    </Card>
</div>

<div class="container">
    <Card>
        {#if isFound}
        <Icon class="material-icons">done</Icon>
        <Label>Trouvé</Label>
        {:else}
            {#if isLower}
                <span>C'est moins</span>
            {:else}
                {#if isUpper}
                    <span>C'est plus</span>
                {/if}
            {/if}
        {/if }
<!-- // ici moins/plus / trouvé -->
</Card>
</div>
<div class="container">
    <Card>
        <Textfield bind:value={currentGuess} label="essai" type="number"/>
        <Button on:click={guess}>            
            <Label>Essayer</Label>
        </Button>        
    </Card>
</div>

