<script>
    import Card from '@smui/card';
    import Fab, { Icon } from '@smui/fab';

    let notify = function() {


        var notificationTitle = 'Hello';
  var notificationOptions = {
    body: 'une notification qu"elle est belle !.',
    icon: './images/icons/icon-192x192.png',
    badge: '',
    tag: '',
    data: {
      url: 'https://developers.google.com/web/fundamentals/getting-started/push-notifications/'
    },
    text:'une notification'
  };


        navigator.serviceWorker.getRegistration()
        .then(reg => reg.showNotification("une notification", notificationOptions));
    }

</script>

<div class="container">
    <div style="margin: 0 auto; width: 50%;  flex-wrap:wrap; padding: 10px;display:flex;flex-direction: column;">
        <Fab on:click={notify} class="row">
          <Icon class="material-icons">message</Icon>
        </Fab>
        </div>
</div>