<script>
  import Counter from './Counter.svelte'
  import Switcher from './switcher.svelte'
  import Game from './game.svelte'
  import Game2 from './game2.svelte'
  import NotFound from './NotFound.svelte'
  import Empty from './empty.svelte'
  import Router from 'svelte-spa-router'
  import {link} from 'svelte-spa-router'
  import Drawer, {AppContent, Content, Header, Title, Subtitle, Scrim} from '@smui/drawer';
  import List, {Item, Text, Separator, Subheader} from '@smui/list';
  import Button, {Label, Icon} from '@smui/button';
  import {push, pop, replace} from 'svelte-spa-router'
  import TopAppBar, {Row, Section} from '@smui/top-app-bar';

  const routes = {
    '/':Empty,
    // Exact path
    // Using named parameters, with last being optional
    '/counter': Counter,

    // Wildcard parameter
    '/switch': Switcher,
    '/game': Game,
    '/game2': Game2,
    // Catch-all
    // This is optional, but if present it must be the last
    '*': NotFound,
}

let drawer;
let drawerOpen = false;


function toggleDrawer() {
  drawerOpen = ! drawerOpen;
  console.log("toggling drawer to -> "+drawerOpen);
}

function toggleAndMove(to) {
  toggleDrawer(); 
  push(to);
}

let prominent = false;
  let dense = false;
  let secondaryColor = false;

</script>
<TopAppBar variant="static" {prominent} {dense} color={secondaryColor ? 'secondary' : 'primary'}>
  <Row>
    <Section>
      <Button on:click={toggleDrawer}>
        <Icon class="material-icons">menu</Icon>  
      </Button> 
      
    </Section>
    <Section align="end" toolbar>
      <span>YEP ! YEP ! YEP !</span>
    </Section>
  </Row>
</TopAppBar>
<Drawer variant="modal" bind:this={drawer} bind:open={drawerOpen}>
  <Header>
    <Title>YEP</Title>
    <Subtitle>yep yep yep !</Subtitle>
  </Header>
  <Content>    
    <List>
      <Item href="/" on:click={toggleDrawer}>
        <Button>
          <Icon class="material-icons">home</Icon>  
        </Button>
        </Item>

      <Separator></Separator>
      <Item on:click={() => toggleAndMove('/counter')}>
        <Button>counter</Button>
      </Item>
      <Item on:click={() => toggleAndMove('/switch')}>
        <Button>switch</Button>
      </Item>
      <Item on:click={() => toggleAndMove('/game')}>
        <Button>jouer</Button>
      </Item>
      <Item on:click={() => toggleAndMove('/game2')}>
        <Button>jouer 2</Button>
      </Item>
    </List>
  </Content>
</Drawer>
<Scrim />
<AppContent>

<Router {routes}/>
</AppContent>



