<script>
  import Card from '@smui/card';
  // import Button, {Label} from '@smui/button';
  import { count } from './store.js';
  import Fab, { Icon } from '@smui/fab';


  count.useLocalStorage();

  let clicked = 0;

  let inc = function () {
    count.update(n => n + 1);
  }
  let dec = function () {
    if (clicked > 0) {
      count.update(n => n - 1);
    }
  }
  let reset = function () {
    count.set(0);
  }

  const unsubscribe = count.subscribe(value => {
    clicked = value;
  });

</script>

<style>
  /* .row {
    flex-direction: row;
    display:flex;
    margin-bottom: 50;
  } */
</style>


<div class="container">
  <!-- <Card> -->
    <div style="margin: 0 auto; width: 50%;  flex-wrap:wrap; padding: 10px;display:flex;flex-direction: column;">
      <Fab on:click={reset} class="row">
        <Icon class="material-icons">autorenew</Icon>
      </Fab>
      
      <!-- <Button on:click={() => clicked=0}>
        <Icon class="material-icons" style="font-size: xx-large;">autorenew</Icon>     
      </Button> -->
      <Fab on:click={dec} class="row">
        <Icon class="material-icons">remove_circle</Icon>
      </Fab>
      <!-- <Button on:click={dec} style="float:right">
        <Icon class="material-icons" style="font-size: xx-large;">remove_circle</Icon>     
      </Button> -->
      <Fab on:click={inc} class="row">
        <Icon class="material-icons">add_circle</Icon>
      </Fab>
      <!-- <Button on:click={inc} style="float:right">
      <Icon class="material-icons" style="font-size: xx-large;">add_circle</Icon>     
    </Button> -->
      <p class="mdc-typography--body1" style="text-align: center;">
        {#if clicked}
        <h1 style="text-align: center;">{clicked}</h1>
      {:else}
        <h1 class="grayed" style="text-align: center;">0</h1>
      {/if}
    </p>
  </div>
  <!-- </Card> -->
  </div>