<script>

    import Card from '@smui/card';
    import Button, {Label} from '@smui/button';
    import Fab, { Icon } from '@smui/fab';
    
    import Textfield from '@smui/textfield'
    import { onMount } from 'svelte';
    import Key from './Key.svelte';
    import NumPad from './NumPad.svelte';

let toBeGuessed = 0;
let guesses = 0;
let isFound = false;
let isLower = false;
let isUpper = false;
let currentGuess = "votre essai";
let trying = false;

let typed=0;

let tried = false;

onMount(async () => {
            reset();
        });

function type(x) {    
    if (tried) {
        typed = 0;
    }    
    tried = false;
    console.log('before',typed);
    typed = typed * 10 + x;    
    console.log('after',typed);
}



function reset() {
    toBeGuessed = Math.floor(Math.random() * Math.floor(100));
    guesses = 0;
    typed=0;  
    isFound = false;
    isLower = false;
    isUpper = false;  
}

function guess() {
    guesses++;    
    console.log(`${typed} ?? ${toBeGuessed}`);
    isFound = toBeGuessed == typed;
    isLower = toBeGuessed < typed;
    isUpper = toBeGuessed > typed;
    tried= true;
}

function onKeyClicked(data) {
  console.log("clicked:",data)
  type(parseInt(data.detail.number,10));
}

</script>

<style>
.button {
    margin : 10px 10px 10px 10px;
}

</style>

<div class="container">
    <Card>
        <!-- ici bouton reset -->
        <Fab on:click={reset} class="row">
            <Icon class="material-icons">autorenew</Icon>
        </Fab>
            
        <!-- ici nombre d'essais -->
        <span>{guesses} essai{guesses > 0 ? 's':''}</span>
    </Card>
</div>

<div class="container">
    <Card>
        <div style="margin: 0 auto; width: 100%;  flex-wrap:wrap; padding: 10px;display:flex;flex-direction: row;">
            
            
        </div>
        
          

        <div style="margin: 0 auto; width: 100%;  flex-wrap:wrap; padding: 10px;display:flex;flex-direction: row;">
            {#if isFound}
            <Icon class="material-icons">done</Icon>
            <Label>Trouvé</Label>
            {:else}
            <div style="width: 33%;">
                {#if isLower}
                <Icon class="material-icons">chevron_left</Icon>
                {/if}
            </div>

        <div style="text-align: center;width: 33%;">
            <Button variant="raised" ripple="true" color="secondary" on:click={guess} class="row">
                <Label>{typed}</Label>
            <!-- <Label></Label> -->
            </Button>
        </div>
        <div style="width: 33%;">
            {#if isUpper}
            <Icon class="material-icons">chevron_right</Icon>
            {/if}
        </div>
        
        {/if}

    </div>
</Card>
</div>
<div class="container">
    <Card>

            <NumPad on:key={onKeyClicked} on:clear={() => {typed=0} }></NumPad>
         
    </Card>
</div>

